import Directory from "../../components/directory/directory.component";
import CarouselBanner from "../../components/banner/Banner";
import Video from '../../components/Videos/Videos'
import Ads from "../../components/Ads/Ads";
import { getCategorie } from "../../utils/firebase/firebase.utils";
import { useState , useEffect } from "react";
import { ProductBox, ProductScroller } from "../../components/ProductScroller/ProductScroller";



function Home() {

  const [categories, setCategories] = useState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const data = await getCategorie();
        setCategories(data);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    }
    fetchData();
  }, []);


  return (
    <div>
      <CarouselBanner />
      <Directory categories={categories} />
      <ProductScroller/>
      <ProductBox/>
      <Video/>
      <Ads/>
    </div>
  );
}

export default Home;
