import { useDraggable } from "react-use-draggable-scroll";
import './ProductScroller.scss'
import { getProductById } from "../../utils/firebase/firebase.utils";
import React, { useState, useEffect, useRef } from 'react';


export function ProductScroller() {
  const ref = useRef();
  const { events } = useDraggable(ref);

  const [products, setProducts] = useState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const data = await getProductById();
        // رندوم‌سازی آرایه محصولات
        const randomProducts = shuffleArray(data).slice(0, 10);
        setProducts(randomProducts);
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    }
    fetchData();
  }, []);

  // تابعی برای رندوم‌سازی یک آرایه
  function shuffleArray(array) {
    return array.sort(() => Math.random() - 0.5);
  }

  return (
    <div className='Product_Scroller' {...events} ref={ref}>
      <div className='Product_Container'>
        <div className='Product_Simbol'>
          <img src='https://www.digikala.com/statics/img/svg/specialCarousel/Amazings.svg' alt=''/>
          <img src='https://www.digikala.com/statics/img/png/specialCarousel/box.webp' alt='' />
          <span>
            <p>مشاهده همه</p>
          </span>
        </div>
        {products.map((product, index) => (
          <div key={index} className='Product'>
            <img src={product.imageUrl[0]} alt='' />
            <h6>{product.name}</h6>
          </div>
        ))}
      </div>
    </div>
  );
}


  
  export function ProductBox() {
    const [products, setProducts] = useState({
      category1: [],
      category2: [],
      category3: []
    });
  
    // دیکشنری برای نام‌گذاری دسته‌ها به صورت فارسی
    const categoryNames = {
      category1: 'تخفیف ها',
      category2: 'پرفروش ها',
      category3: 'جدید ترین ها'
    };
  
    useEffect(() => {
      async function fetchData() {
        try {
          const data = await getProductById();
          // تقسیم محصولات به سه دسته
          const randomProducts = shuffleArray(data); // تابعی برای رندوم‌سازی آرایه محصولات
          const category1Products = randomProducts.slice(0, 4);
          const category2Products = randomProducts.slice(4, 8);
          const category3Products = randomProducts.slice(8, 12);
  
          // تنظیم محصولات در هر دسته
          setProducts({
            category1: category1Products,
            category2: category2Products,
            category3: category3Products
          });
        } catch (error) {
          console.error("Error fetching categories:", error);
        }
      }
      fetchData();
    }, []);
  
    // تابعی برای رندوم‌سازی یک آرایه
    function shuffleArray(array) {
      return array.sort(() => Math.random() - 0.5);
    }
  
    return (
      <div className='ProductBox'>
        {Object.keys(products).map((category, index) => (
          <div key={index} className='product_Container'>
            <p className='title'>{categoryNames[category]}</p> {/* استفاده از نام‌های فارسی دسته‌ها */}
            <p className='title2'>براساس بازدید های شما</p>
  
            {products[category].map((product, index) => (
              <div key={index} className='product'>
                <img src={product.imageUrl[0]} alt='' />
              </div>
            ))}
  
            <p className='showmore'>مشاهده</p>
          </div>
        ))}
      </div>
    );
  }
  
  