import React, { useState, useEffect, useRef } from 'react';
import './Ads.css';

const Ads = () => {
  const [activeTab, setActiveTab] = useState(1); // Starting tab
  const dotElements = useRef([]);
  const cirItemElements = useRef([]);

  useEffect(() => {
      const fields = Array.from(document.querySelectorAll('.itemDot'));
      const container = document.querySelector('.dotCircle');
      const width = container.offsetWidth;
      const height = container.offsetHeight;
      const radius = width / 2.5;
    const step = (2 * Math.PI) / fields.length;

    fields.forEach((field, index) => {
      const x = Math.round(width / 2 + radius * Math.cos(index * step) - field.offsetWidth / 2);
      const y = Math.round(height / 2 + radius * Math.sin(index * step) - field.offsetHeight / 2);

      field.style.left = `${x}px`;
      field.style.top = `${y}px`;
    });

    dotElements.current = fields;
    cirItemElements.current = Array.from(document.querySelectorAll('.itemDot'));

    const interval = setInterval(() => {
      setActiveTab((prevTab) => {
        const nextTab = prevTab >= 6 ? 1 : prevTab + 1;
        container.style.transform = `rotate(${ (nextTab) * 36}deg)`;
        container.style.transition = '2s';

        return nextTab;
      });
    }, 5000);


    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className='ads'>
      <section className="iq-features">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-3 col-md-12"></div>
            <div className="col-lg-6 col-md-12">
              <div className="holderCircle">
                <div className="round"></div>
                <div className="dotCircle">
                  <span onClick={() => setActiveTab(1)} className={`itemDot ${activeTab === 1 ? 'active' : ''}`}>
                    <i className="fa fa-clock-o"></i>
                    <span className="forActive"></span>
                  </span>
                  <span onClick={() => setActiveTab(2)} className={`itemDot ${activeTab === 2 ? 'active' : ''}`}>
                    <i className="fa fa-comments"></i>
                    <span className="forActive"></span>
                  </span>
                  <span onClick={() => setActiveTab(3)} className={`itemDot ${activeTab === 3 ? 'active' : ''}`}>
                    <i className="fa fa-user"></i>
                    <span className="forActive"></span>
                  </span>
                  <span onClick={() => setActiveTab(4)} className={`itemDot ${activeTab === 4 ? 'active' : ''}`}>
                  <i class="fa fa-credit-card-alt" aria-hidden="true"></i>
                    <span className="forActive"></span>
                  </span>
                  <span onClick={() => setActiveTab(5)} className={`itemDot ${activeTab === 5 ? 'active' : ''}`}>
                    <i className="fa fa-upload"></i>
                    <span className="forActive"></span>
                  </span>
                  <span onClick={() => setActiveTab(6)} className={`itemDot ${activeTab === 6 ? 'active' : ''}`}>
                  <i class="fas fa-plane"></i>
                    <span className="forActive"></span>
                  </span>
                </div>
                <div className="contentCircle">
                    <div className={`CirItem title-box ${activeTab === 1 ? 'active' : ''} CirItem${1}`}>
                      <h2 className="title"><span>پشتیبانی 24 ساعته</span></h2>
                      <i className="fa fa-clock-o"></i>
                    </div>
                    <div className={`CirItem title-box ${activeTab === 2 ? 'active' : ''} CirItem${2}`}>
                      <h2 className="title"><span>مشاوره آنلاین</span></h2>
                      <i className="fa fa-comments"></i>
                    </div>
                    <div className={`CirItem title-box ${activeTab === 3 ? 'active' : ''} CirItem${3}`}>
                      <h2 className="title"><span>امکان ساخت پروفایل خصوصی</span></h2>
                      <i className="fa fa-user"></i>
                    </div>
                    <div className={`CirItem title-box ${activeTab === 4 ? 'active' : ''} CirItem${4}`}>
                      <h2 className="title"><span>پرداخت امن و آسان</span></h2>
                      <i class="fa fa-credit-card-alt" aria-hidden="true"></i>
                    </div>
                    <div className={`CirItem title-box ${activeTab === 5 ? 'active' : ''} CirItem${5}`}>
                      <h2 className="title"><span>زمانت بازگشت کالا</span></h2>
                      <i className="fa fa-upload"></i>
                    </div>
                    <div className={`CirItem title-box ${activeTab === 6 ? 'active' : ''} CirItem${6}`}>
                      <h2 className="title"><span>ارسال امن و سریع</span></h2>
                      <i class="fas fa-plane"></i>
                    </div>
                </div>
              </div>
            </div>
            <div className="col-lg-3 col-md-12"></div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Ads;
