import React, { useEffect, useRef } from 'react';
import './Videos.css'
import { useDraggable } from 'react-use-draggable-scroll'


export default function Videos() {
    
    const ref = useRef(); 
    const { events } = useDraggable(ref);


    const observerCallback = (entries) => {
        entries.forEach((entry) => {
          const animation = entry.target.getAttribute('data-animation');
    
          if (entry.isIntersecting) {
            entry.target.classList.add('animated', animation);
          } else {
            entry.target.classList.remove('animated', animation);
          }
        });
      };
    
      const observerRef = useRef(null);
    
      useEffect(() => {
        const animatedEls = document.querySelectorAll('[data-animation]');
    
        observerRef.current = new IntersectionObserver(observerCallback);
    
        animatedEls.forEach((el) => observerRef.current.observe(el));
    
        return () => {
          if (observerRef.current) {
            observerRef.current.disconnect();
          }
        };
      }, [observerCallback]);


  return (
    <div className='Videos'>
      <div className="recipe-container">
        <h3>ویدیو های پر بازدید</h3>
        <div className="swiper"  {...events} ref={ref}>
          <div className="swiper-wrapper">
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '.5s' }}>
            <iframe className='post-video' width='max-content' src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </div>
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '1.5s' }}>
            <iframe className='post-video' width='max-content' src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </div>
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '2.5s' }}>
            <iframe className='post-video' width='max-content' src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </div>
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '3.5s' }}>
            <iframe className='post-video' src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>            </div>
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '4.5s' }}>
            <iframe  src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>            </div>
            <div className="swiper-slide post" data-animation="animated" style={{ animationDuration: '5.5s' }}>
            <iframe className='post-video'  src="https://www.youtube.com/embed/oMWznTxh2pQ?list=TLGG_XHiWRSyd_UyNDAxMjAyNA" title="3700Pro™  Espresso Machine | Set up with your espresso machine: A complete walkthrough | Casabrews" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>            </div>
          </div>
          <div className="swiper-scrollbar"></div>
        </div>
      </div>
    </div>
  )
}
